










































import { Vue, Component } from 'vue-property-decorator'

import SidebarComp from '../components/sidebarComp'

@Component({
    components: { ...SidebarComp }
})
export default class App extends Vue {
    // 
    items = [
        { title: '', comp: 'button-edit', icon: 'mdi-plus-box-multiple', persistent: true },
        { title: '', comp: 'button-config', icon: 'mdi-cursor-default-click', persistent: false },
        { title: ' shard', comp: 'shard-setter', icon: 'mdi-server', persistent: false },
        { title: '', comp: 'save-config', icon: 'mdi-download-multiple', persistent: false },
        { title: '', comp: 'load-config', icon: 'mdi-upload-multiple', persistent: false },
        { title: '', comp: 'about', icon: 'mdi-help-box', persistent: false }
    ]

    // 
    showSideDrawer = false
    //  dialog
    showSidebar = false
    //  dialog 
    sidebarPersistent = false

    // 
    activeSidebar = 'save-config'

    // 
    showMessage = false
    messageColor = 'success'
    messageText = ''

    /**
     *  - 
     *
     * @param compName 
     * @param persistent 
     */
    onClickSidebar(compName: string, persistent: boolean) {
        this.showSidebar = true
        this.showSideDrawer = false
        this.activeSidebar = compName
        this.sidebarPersistent = persistent
    }

    /**
     *  - 
     */
    onSidebarFinish(e: SidebarEmitEvent) {
        this.showSidebar = false

        if (!e.show) return

        this.message(e.color || 'success', e.content || '')
    }

    // 
    message(color: string, text: string) {
        this.messageColor = color
        this.messageText = text
        this.showMessage = true
    }

    mounted() {

    }
}
